import {Component, OnInit, HostListener, ViewChild} from '@angular/core';
import * as $ from 'jquery';
import * as EasyPieChart from '../assets/jquery/easy-pie-chart/dist/jquery.easypiechart.min.js';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit{

  ngOnInit() {
      $('body').css({'overflow': 'hidden'});
      $(document).bind('scroll', function () {
          window.scrollTo(0, 0);
      });
      setTimeout(function(){
          document.getElementById('loading').style.opacity = '0';
          $(document).unbind('scroll');
          $('body').css({'overflow-y': 'visible'});
      }, 3000);
  }
  @HostListener('window:scroll') onScroll() {
      $('.animated').each( function() {
         const elemPos = $(this).offset().top;
         const topWindow = $(window).scrollTop();
         if (elemPos < topWindow + 600) {
             $(this).removeClass('isPaused');
         }
      });
      // circle progress bar
      if ($().easyPieChart) {
          var count = 0 ;
          const colors = ['#a8d164', '#fbcf61', '#ff6f6f'];
          $('.chart').each(function() {
              const imagePos = $(this).offset().top;
              const topOfWindow = $(window).scrollTop();
              if (imagePos < topOfWindow + 600) {

                  $(this).easyPieChart({
                      barColor: colors[count],
                      trackColor: '#343434',
                      scaleColor: false,
                      scaleLength: false,
                      lineCap: 'butt',
                      lineWidth: 10,
                      size: 130,
                      rotate: 0,
                      animate: 2000,
                      onStep: function(from, to, percent) {
                          $(this.el).find('.percent').text(Math.round(percent));
                      }
                  });
              }
              count++;
              if (count >= colors.length) { count = 0; }
          });
      }
      try {
          const element = document.querySelector('.chart');
          new EasyPieChart(element, {
              // your options goes here
          });
      } catch(e) {
          console.log(''); // it will catch an error, just log nothing
      }
  }

  homeScroll() {
      const homePos = document.getElementById('home').offsetTop;
      window.scrollTo({ left: 0, top: homePos, behavior: 'smooth' });
  }
  startScroll() {
      const aboutPos = document.getElementById('about').offsetTop;
      window.scrollTo({ left: 0, top: aboutPos, behavior: 'smooth' });
  }
}
