import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-contact-form',
  templateUrl: './app-contact-form.component.html',
  styleUrls: ['./app-contact-form.component.scss']
})
export class AppContactFormComponent implements OnInit {

    message: string;
    email: string;
    name: string;
    endpoint: string;

    constructor() {
    }

  ngOnInit() {

      this.email = '';
      this.endpoint = 'http://localhost:3000/contact_me.php';

      document.getElementById('contactForm').addEventListener('submit', function(e) {

          // this.email = document.getElementById('name').value);

          e.preventDefault();
          document.getElementById('sendMessageButton').classList.add('onclic');

          setTimeout(function(){
            validate();
          }, 250);

          function validate() {
              setTimeout(function() {
                  document.getElementById('sendMessageButton').classList.remove('onclic');
                  document.getElementById('sendMessageButton').classList.add('validate');
              }, 2250 );
          }

          setTimeout(function(){
              callback();
          }, 450);

          function callback() {
              setTimeout(function() {
                  document.getElementById('sendMessageButton').classList.remove('validate');
              }, 1250 );
          }

          setTimeout(function(){
              const contactForm = <HTMLFormElement>document.getElementById('contactForm');
              // contactForm.submit();
          }, 3400);
      });
  }

}
